import { useState, useEffect, useRef } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import Editor from '@monaco-editor/react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { ScrollArea } from '@/components/ui/scroll-area'
import { Input } from '@/components/ui/input'
import { Slider } from '@/components/ui/slider'
import { Switch } from '@/components/ui/switch'
import { Label } from '@/components/ui/label'
import { Separator } from '@/components/ui/separator'
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from '@/components/ui/dialog'
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
  DropdownMenuSeparator,
} from '@/components/ui/dropdown-menu'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'
import {
  Tooltip,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from '@/components/ui/tooltip'
import {
  Copy,
  Check,
  Code2,
  Layout,
  Save,
  Trash2,
  Download,
  Terminal,
  Palette,
  FolderOpen,
  Eye,
  Smartphone,
  Tablet,
  Monitor,
  RotateCcw,
  Search,
  Share2,
  Heart,
  History,
  Settings,
  RefreshCw,
  ChevronDown,
  MoreHorizontal,
  Paintbrush,
} from 'lucide-react'
import { toast } from 'sonner'
import { Navigation } from '@/components/home/Navigation'
import { defaultTemplates, iconMap } from '@/data/codeTemplates'
import { Footer } from '@/components/home/Footer'

// ============================================
// 
// ============================================
interface CodeTemplate {
  id: string
  name: string
  description: string
  category: string
  icon: string
  preview?: string
  code: {
    html: string
    css: string
    js: string
  }
}

interface SavedSnippet {
  id: string
  name: string
  code: {
    html: string
    css: string
    js: string
  }
  createdAt: number
  isFavorite?: boolean
}

interface EditorSettings {
  fontSize: number
  wordWrap: 'on' | 'off' | 'wordWrapColumn' | 'bounded'
  minimap: boolean
  lineNumbers: 'on' | 'off' | 'relative' | 'interval'
  tabSize: number
}

type DeviceType = 'desktop' | 'tablet' | 'mobile'
type ViewMode = 'split' | 'preview' | 'editor'

// ============================================
// 
// ============================================
export function CodePlaygroundPage() {
  // 
  const [activeTab, setActiveTab] = useState<'html' | 'css' | 'js'>('html')
  const [code, setCode] = useState(defaultTemplates[0].code)
  // const [isFullscreen, setIsFullscreen] = useState(false)
  const [deviceType, setDeviceType] = useState<DeviceType>('desktop')
  const [zoom, setZoom] = useState(100)
  const [savedSnippets, setSavedSnippets] = useState<SavedSnippet[]>([])
  const [snippetName, setSnippetName] = useState('')
  const [showSaveDialog, setShowSaveDialog] = useState(false)
  const [copied, setCopied] = useState(false)
  const [consoleLogs, setConsoleLogs] = useState<
    { type: string; message: string; timestamp: number }[]
  >([])
  const [showConsole, setShowConsole] = useState(false)
  const [selectedTemplate, setSelectedTemplate] = useState<CodeTemplate>(
    defaultTemplates[0] as CodeTemplate
  )
  // const [error, setError] = useState<Error | null>(null)
  const [editorTheme, setEditorTheme] = useState<'vs-dark' | 'light'>('vs-dark')
  const [autoPreview, setAutoPreview] = useState(true)
  const [hasUnsavedChanges, setHasUnsavedChanges] = useState(false)
  const [viewMode, setViewMode] = useState<ViewMode>('split')
  const [searchQuery, setSearchQuery] = useState('')
  const [selectedCategory, setSelectedCategory] = useState('')
  const [showSettings, setShowSettings] = useState(false)
  const [editorSettings, setEditorSettings] = useState<EditorSettings>({
    fontSize: 14,
    wordWrap: 'on',
    minimap: false,
    lineNumbers: 'on',
    tabSize: 2,
  })
  // const [isLoading, setIsLoading] = useState(false)
  const [previewKey, setPreviewKey] = useState(0)

  const iframeRef = useRef<HTMLIFrameElement>(null)
  // const fileInputRef = useRef<HTMLInputElement>(null)

  //  localStorage 
  useEffect(() => {
    const saved = localStorage.getItem('code-snippets')
    if (saved) {
      try {
        setSavedSnippets(JSON.parse(saved))
      } catch (e) {
        console.error('Failed to load snippets:', e)
      }
    }
  }, [])

  //  localStorage
  useEffect(() => {
    localStorage.setItem('code-snippets', JSON.stringify(savedSnippets))
  }, [savedSnippets])

  // 
  useEffect(() => {
    setHasUnsavedChanges(true)
  }, [code])

  // 
  const generatePreviewContent = () => {
    return `
            <!DOCTYPE html>
            <html>
              <head>
                <meta charset="UTF-8">
                <meta name="viewport" content="width=device-width, initial-scale=1.0">
                <title>Preview</title>
                <script src="https://unpkg.com/react@18/umd/react.development.js"></script>
                <script src="https://unpkg.com/react-dom@18/umd/react-dom.development.js"></script>
                <script src="https://unpkg.com/@babel/standalone/babel.min.js"></script>
                <script src="https://cdn.tailwindcss.com"></script>
                <style>
                  ${code.css}
                </style>
              </head>
              <body>
                <div id="root"></div>
                <script type="text/babel">
                  const { useState, useEffect, useCallback, useMemo, useRef } = React;
                  
                  function App() {
                    ${code.js}
                    
                    return (
                      <>
                        ${code.html}
                      </>
                    );
                  }
                  
                  const root = ReactDOM.createRoot(document.getElementById('root'));
                  root.render(<App />);
                </script>
              </body>
            </html>
    `.trim()
  }

  // 
  const refreshPreview = () => {
    setPreviewKey(prev => prev + 1)
  }

  // 
  const handleCodeChange = (
    value: string | undefined,
    type: 'html' | 'css' | 'js'
  ) => {
    setCode(prev => ({
      ...prev,
      [type]: value || '',
    }))
    setHasUnsavedChanges(true)
    if (autoPreview) {
      refreshPreview()
    }
  }

  // 
  const copyCode = async () => {
    const fullCode = `<!-- HTML -->\n${code.html}\n\n/* CSS */\n${code.css}\n\n// JavaScript\n${code.js}`
    await navigator.clipboard.writeText(fullCode)
    setCopied(true)
    toast.success('')
    setTimeout(() => setCopied(false), 2000)
  }

  // 
  const downloadCode = () => {
    const fullCode = `<!DOCTYPE html>
                      <html>
                      <head>
                        <meta charset="UTF-8">
                        <title>Code Playground Export</title>
                        <script src="https://cdn.tailwindcss.com"></script>
                        <style>
                      ${code.css}
                        </style>
                      </head>
                      <body>
                      ${code.html}
                      <script>
                      ${code.js}
                      </script>
                      </body>
                      </html>`
    const blob = new Blob([fullCode], { type: 'text/html' })
    const url = URL.createObjectURL(blob)
    const a = document.createElement('a')
    a.href = url
    a.download = 'playground-export.html'
    a.click()
    URL.revokeObjectURL(url)
    toast.success('')
  }

  // 
  const saveSnippet = () => {
    if (!snippetName.trim()) {
      toast.error('')
      return
    }
    const newSnippet: SavedSnippet = {
      id: Date.now().toString(),
      name: snippetName,
      code: { ...code },
      createdAt: Date.now(),
    }
    setSavedSnippets(prev => [newSnippet, ...prev])
    setSnippetName('')
    setShowSaveDialog(false)
    setHasUnsavedChanges(false)
    toast.success('')
  }

  // 
  const loadSnippet = (snippet: SavedSnippet) => {
    setCode(snippet.code)
    setHasUnsavedChanges(false)
    refreshPreview()
    toast.success(`: ${snippet.name}`)
  }

  // 
  const deleteSnippet = (id: string) => {
    setSavedSnippets(prev => prev.filter(s => s.id !== id))
    toast.success('')
  }

  // 
  const toggleFavorite = (id: string) => {
    setSavedSnippets(prev =>
      prev.map(s => (s.id === id ? { ...s, isFavorite: !s.isFavorite } : s))
    )
  }

  // 
  const resetCode = () => {
    setCode(selectedTemplate.code)
    setHasUnsavedChanges(false)
    refreshPreview()
    toast.success('')
  }

  // 
  const formatCode = () => {
    // 
    const formattedHtml = code.html.replace(/>\s*</g, '>\n<').trim()
    const formattedCss = code.css
      .replace(/;/g, ';\n')
      .replace(/{/g, ' {\n')
      .replace(/}/g, '\n}')
      .trim()
    const formattedJs = code.js.replace(/;/g, ';\n').trim()

    setCode({
      html: formattedHtml,
      css: formattedCss,
      js: formattedJs,
    })
    toast.success('')
  }

  // 
  const shareCode = () => {
    const codeData = btoa(JSON.stringify(code))
    const shareUrl = `${window.location.origin}${window.location.pathname}?code=${codeData}`
    navigator.clipboard.writeText(shareUrl)
    toast.success('')
  }

  // 
  const loadTemplate = (template: CodeTemplate) => {
    setSelectedTemplate(template)
    setCode(template.code)
    setHasUnsavedChanges(false)
    refreshPreview()
    toast.success(`: ${template.name}`)
  }

  // 
  const categories = [
    '',
    ...Array.from(new Set(defaultTemplates.map(t => t.category))),
  ]

  // 
  const filteredTemplates = defaultTemplates.filter(template => {
    const matchesSearch = template.name
      .toLowerCase()
      .includes(searchQuery.toLowerCase())
    const matchesCategory =
      selectedCategory === '' || template.category === selectedCategory
    return matchesSearch && matchesCategory
  })

  // 
  const getIconComponent = (iconName: string) => {
    const IconComponent = iconMap[iconName]
    return IconComponent ? (
      <IconComponent className="w-4 h-4" />
    ) : (
      <Code2 className="w-4 h-4" />
    )
  }

  // 
  const deviceSizes = {
    desktop: '100%',
    tablet: '768px',
    mobile: '375px',
  }

  return (
    <div className="min-h-screen bg-linear-to-br from-slate-50 to-slate-100 dark:from-slate-900 dark:to-slate-800">
      <Navigation />
      <div className="pt-20 pb-8 px-4 sm:px-6 lg:px-8">
        {/*  */}
        <motion.div
          className="max-w-400 mx-auto mb-6"
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
        >
          <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-4">
            <div>
              <h1 className="text-3xl font-bold bg-linear-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
                
              </h1>
              <p className="text-slate-500 dark:text-slate-400 mt-1">
                
              </p>
            </div>

            {/*  */}
            <div className="flex flex-wrap items-center gap-2">
              {/*  */}
              <div className="flex bg-white dark:bg-slate-800 rounded-lg p-1 shadow-sm border">
                <TooltipProvider>
                  <Tooltip>
                    <TooltipTrigger asChild>
                      <Button
                        variant={viewMode === 'editor' ? 'default' : 'ghost'}
                        size="sm"
                        onClick={() => setViewMode('editor')}
                        className="h-8"
                      >
                        <Code2 className="w-4 h-4" />
                      </Button>
                    </TooltipTrigger>
                    <TooltipContent></TooltipContent>
                  </Tooltip>
                </TooltipProvider>

                <TooltipProvider>
                  <Tooltip>
                    <TooltipTrigger asChild>
                      <Button
                        variant={viewMode === 'split' ? 'default' : 'ghost'}
                        size="sm"
                        onClick={() => setViewMode('split')}
                        className="h-8"
                      >
                        <Layout className="w-4 h-4" />
                      </Button>
                    </TooltipTrigger>
                    <TooltipContent></TooltipContent>
                  </Tooltip>
                </TooltipProvider>

                <TooltipProvider>
                  <Tooltip>
                    <TooltipTrigger asChild>
                      <Button
                        variant={viewMode === 'preview' ? 'default' : 'ghost'}
                        size="sm"
                        onClick={() => setViewMode('preview')}
                        className="h-8"
                      >
                        <Eye className="w-4 h-4" />
                      </Button>
                    </TooltipTrigger>
                    <TooltipContent></TooltipContent>
                  </Tooltip>
                </TooltipProvider>
              </div>

              <Separator orientation="vertical" className="h-8" />

              {/*  */}
              <div className="flex bg-white dark:bg-slate-800 rounded-lg p-1 shadow-sm border">
                <TooltipProvider>
                  <Tooltip>
                    <TooltipTrigger asChild>
                      <Button
                        variant={deviceType === 'desktop' ? 'default' : 'ghost'}
                        size="sm"
                        onClick={() => setDeviceType('desktop')}
                        className="h-8"
                      >
                        <Monitor className="w-4 h-4" />
                      </Button>
                    </TooltipTrigger>
                    <TooltipContent></TooltipContent>
                  </Tooltip>
                </TooltipProvider>

                <TooltipProvider>
                  <Tooltip>
                    <TooltipTrigger asChild>
                      <Button
                        variant={deviceType === 'tablet' ? 'default' : 'ghost'}
                        size="sm"
                        onClick={() => setDeviceType('tablet')}
                        className="h-8"
                      >
                        <Tablet className="w-4 h-4" />
                      </Button>
                    </TooltipTrigger>
                    <TooltipContent></TooltipContent>
                  </Tooltip>
                </TooltipProvider>

                <TooltipProvider>
                  <Tooltip>
                    <TooltipTrigger asChild>
                      <Button
                        variant={deviceType === 'mobile' ? 'default' : 'ghost'}
                        size="sm"
                        onClick={() => setDeviceType('mobile')}
                        className="h-8"
                      >
                        <Smartphone className="w-4 h-4" />
                      </Button>
                    </TooltipTrigger>
                    <TooltipContent></TooltipContent>
                  </Tooltip>
                </TooltipProvider>
              </div>

              <Separator orientation="vertical" className="h-8" />

              {/*  */}
              <TooltipProvider>
                <Tooltip>
                  <TooltipTrigger asChild>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={refreshPreview}
                      className="h-8"
                    >
                      <RefreshCw className="w-4 h-4" />
                    </Button>
                  </TooltipTrigger>
                  <TooltipContent></TooltipContent>
                </Tooltip>
              </TooltipProvider>

              <TooltipProvider>
                <Tooltip>
                  <TooltipTrigger asChild>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={copyCode}
                      className="h-8"
                    >
                      {copied ? (
                        <Check className="w-4 h-4" />
                      ) : (
                        <Copy className="w-4 h-4" />
                      )}
                    </Button>
                  </TooltipTrigger>
                  <TooltipContent></TooltipContent>
                </Tooltip>
              </TooltipProvider>

              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="outline" size="sm" className="h-8">
                    <MoreHorizontal className="w-4 h-4" />
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end">
                  <DropdownMenuItem onClick={formatCode}>
                    <Paintbrush className="w-4 h-4 mr-2" />
                    
                  </DropdownMenuItem>
                  <DropdownMenuItem onClick={resetCode}>
                    <RotateCcw className="w-4 h-4 mr-2" />
                    
                  </DropdownMenuItem>
                  <DropdownMenuSeparator />
                  <DropdownMenuItem onClick={downloadCode}>
                    <Download className="w-4 h-4 mr-2" />
                    
                  </DropdownMenuItem>
                  <DropdownMenuItem onClick={shareCode}>
                    <Share2 className="w-4 h-4 mr-2" />
                    
                  </DropdownMenuItem>
                  <DropdownMenuSeparator />
                  <DropdownMenuItem onClick={() => setShowSaveDialog(true)}>
                    <Save className="w-4 h-4 mr-2" />
                    
                  </DropdownMenuItem>
                  <DropdownMenuItem onClick={() => setShowSettings(true)}>
                    <Settings className="w-4 h-4 mr-2" />
                    
                  </DropdownMenuItem>
                </DropdownMenuContent>
              </DropdownMenu>
            </div>
          </div>
        </motion.div>

        {/*  */}
        <div className="max-w-400 mx-auto">
          <div
            className={`grid gap-6 ${viewMode === 'split' ? 'grid-cols-1 lg:grid-cols-2' : 'grid-cols-1'}`}
          >
            {/*  */}
            {(viewMode === 'editor' || viewMode === 'split') && (
              <motion.div
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                className="space-y-4"
              >
                {/*  */}
                <Card className="border-slate-200 dark:border-slate-700">
                  <CardHeader className="pb-3">
                    <CardTitle className="text-sm font-medium flex items-center gap-2">
                      <FolderOpen className="w-4 h-4" />
                      
                    </CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="space-y-3">
                      <div className="flex gap-2">
                        <div className="relative flex-1">
                          <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-slate-400" />
                          <Input
                            placeholder="..."
                            value={searchQuery}
                            onChange={e => setSearchQuery(e.target.value)}
                            className="pl-9"
                          />
                        </div>
                        <Select
                          value={selectedCategory}
                          onValueChange={setSelectedCategory}
                        >
                          <SelectTrigger className="w-35">
                            <SelectValue />
                          </SelectTrigger>
                          <SelectContent>
                            {categories.map(cat => (
                              <SelectItem key={cat} value={cat}>
                                {cat}
                              </SelectItem>
                            ))}
                          </SelectContent>
                        </Select>
                      </div>

                      <ScrollArea className="h-30">
                        <div className="grid grid-cols-2 sm:grid-cols-3 gap-2">
                          {filteredTemplates.map(template => (
                            <motion.button
                              key={template.id}
                              onClick={() =>
                                loadTemplate(template as CodeTemplate)
                              }
                              className={`p-3 rounded-lg border text-left transition-all ${
                                selectedTemplate.id === template.id
                                  ? 'border-blue-500 bg-blue-50 dark:bg-blue-900/20'
                                  : 'border-slate-200 dark:border-slate-700 hover:border-blue-300'
                              }`}
                              whileHover={{ scale: 1.02 }}
                              whileTap={{ scale: 0.98 }}
                            >
                              <div className="flex items-center gap-2 mb-1">
                                {getIconComponent(template.icon as string)}
                                <span className="font-medium text-sm truncate">
                                  {template.name}
                                </span>
                              </div>
                              <p className="text-xs text-slate-500 truncate">
                                {template.description}
                              </p>
                            </motion.button>
                          ))}
                        </div>
                      </ScrollArea>
                    </div>
                  </CardContent>
                </Card>

                {/*  */}
                <Card className="border-slate-200 dark:border-slate-700 overflow-hidden">
                  <Tabs
                    value={activeTab}
                    onValueChange={v =>
                      setActiveTab(v as 'html' | 'css' | 'js')
                    }
                  >
                    <CardHeader className="pb-0">
                      <div className="flex items-center justify-between">
                        <TabsList>
                          <TabsTrigger value="html" className="gap-2">
                            <Code2 className="w-4 h-4" />
                            HTML
                          </TabsTrigger>
                          <TabsTrigger value="css" className="gap-2">
                            <Palette className="w-4 h-4" />
                            CSS
                          </TabsTrigger>
                          <TabsTrigger value="js" className="gap-2">
                            <Terminal className="w-4 h-4" />
                            JavaScript
                          </TabsTrigger>
                        </TabsList>

                        <div className="flex items-center gap-2">
                          <Switch
                            checked={autoPreview}
                            onCheckedChange={setAutoPreview}
                            id="auto-preview"
                          />
                          <Label
                            htmlFor="auto-preview"
                            className="text-sm cursor-pointer"
                          >
                            
                          </Label>
                        </div>
                      </div>
                    </CardHeader>

                    <CardContent className="p-0">
                      <TabsContent value="html" className="mt-0">
                        <Editor
                          height="400px"
                          language="html"
                          value={code.html}
                          onChange={v => handleCodeChange(v, 'html')}
                          theme={editorTheme}
                          options={{
                            minimap: { enabled: editorSettings.minimap },
                            fontSize: editorSettings.fontSize,
                            wordWrap: editorSettings.wordWrap,
                            lineNumbers: editorSettings.lineNumbers,
                            tabSize: editorSettings.tabSize,
                            automaticLayout: true,
                          }}
                        />
                      </TabsContent>

                      <TabsContent value="css" className="mt-0">
                        <Editor
                          height="400px"
                          language="css"
                          value={code.css}
                          onChange={v => handleCodeChange(v, 'css')}
                          theme={editorTheme}
                          options={{
                            minimap: { enabled: editorSettings.minimap },
                            fontSize: editorSettings.fontSize,
                            wordWrap: editorSettings.wordWrap,
                            lineNumbers: editorSettings.lineNumbers,
                            tabSize: editorSettings.tabSize,
                            automaticLayout: true,
                          }}
                        />
                      </TabsContent>

                      <TabsContent value="js" className="mt-0">
                        <Editor
                          height="400px"
                          language="javascript"
                          value={code.js}
                          onChange={v => handleCodeChange(v, 'js')}
                          theme={editorTheme}
                          options={{
                            minimap: { enabled: editorSettings.minimap },
                            fontSize: editorSettings.fontSize,
                            wordWrap: editorSettings.wordWrap,
                            lineNumbers: editorSettings.lineNumbers,
                            tabSize: editorSettings.tabSize,
                            automaticLayout: true,
                          }}
                        />
                      </TabsContent>
                    </CardContent>
                  </Tabs>
                </Card>

                {/*  */}
                {savedSnippets.length > 0 && (
                  <Card className="border-slate-200 dark:border-slate-700">
                    <CardHeader className="pb-3">
                      <CardTitle className="text-sm font-medium flex items-center gap-2">
                        <History className="w-4 h-4" />
                        
                      </CardTitle>
                    </CardHeader>
                    <CardContent>
                      <ScrollArea className="h-37.5">
                        <div className="space-y-2">
                          {savedSnippets.map(snippet => (
                            <div
                              key={snippet.id}
                              className="flex items-center justify-between p-2 rounded-lg border border-slate-200 dark:border-slate-700 hover:bg-slate-50 dark:hover:bg-slate-800"
                            >
                              <button
                                onClick={() => loadSnippet(snippet)}
                                className="flex-1 text-left"
                              >
                                <span className="font-medium text-sm">
                                  {snippet.name}
                                </span>
                                <span className="text-xs text-slate-500 ml-2">
                                  {new Date(
                                    snippet.createdAt
                                  ).toLocaleDateString()}
                                </span>
                              </button>
                              <div className="flex items-center gap-1">
                                <Button
                                  variant="ghost"
                                  size="sm"
                                  onClick={() => toggleFavorite(snippet.id)}
                                >
                                  <Heart
                                    className={`w-4 h-4 ${snippet.isFavorite ? 'fill-red-500 text-red-500' : ''}`}
                                  />
                                </Button>
                                <Button
                                  variant="ghost"
                                  size="sm"
                                  onClick={() => deleteSnippet(snippet.id)}
                                >
                                  <Trash2 className="w-4 h-4 text-red-500" />
                                </Button>
                              </div>
                            </div>
                          ))}
                        </div>
                      </ScrollArea>
                    </CardContent>
                  </Card>
                )}
              </motion.div>
            )}

            {/*  */}
            {(viewMode === 'preview' || viewMode === 'split') && (
              <motion.div
                initial={{ opacity: 0, x: 20 }}
                animate={{ opacity: 1, x: 0 }}
                className="space-y-4"
              >
                <Card className="border-slate-200 dark:border-slate-700">
                  <CardHeader className="pb-3">
                    <div className="flex items-center justify-between">
                      <CardTitle className="text-sm font-medium flex items-center gap-2">
                        <Eye className="w-4 h-4" />
                        
                        {hasUnsavedChanges && (
                          <span className="text-xs text-amber-500">
                            ()
                          </span>
                        )}
                      </CardTitle>

                      <div className="flex items-center gap-2">
                        <span className="text-xs text-slate-500">{zoom}%</span>
                        <Slider
                          value={[zoom]}
                          onValueChange={v => setZoom(v[0])}
                          min={50}
                          max={150}
                          step={10}
                          className="w-24"
                        />
                      </div>
                    </div>
                  </CardHeader>
                  <CardContent>
                    <div className="bg-slate-100 dark:bg-slate-900 rounded-lg p-4 overflow-auto">
                      <div
                        className="mx-auto bg-white dark:bg-slate-800 rounded-lg shadow-lg overflow-hidden transition-all duration-300"
                        style={{
                          width: deviceSizes[deviceType],
                          maxWidth: '100%',
                          transform: `scale(${zoom / 100})`,
                          transformOrigin: 'top center',
                        }}
                      >
                        <iframe
                          ref={iframeRef}
                          key={previewKey}
                          srcDoc={generatePreviewContent()}
                          className="w-full min-h-125 border-0"
                          sandbox="allow-scripts allow-same-origin"
                          title="preview"
                        />
                      </div>
                    </div>
                  </CardContent>
                </Card>

                {/*  */}
                <Card className="border-slate-200 dark:border-slate-700">
                  <CardHeader className="pb-3">
                    <div className="flex items-center justify-between">
                      <CardTitle
                        className="text-sm font-medium flex items-center gap-2 cursor-pointer"
                        onClick={() => setShowConsole(!showConsole)}
                      >
                        <Terminal className="w-4 h-4" />
                        
                        <ChevronDown
                          className={`w-4 h-4 transition-transform ${showConsole ? 'rotate-180' : ''}`}
                        />
                      </CardTitle>

                      {consoleLogs.length > 0 && (
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => setConsoleLogs([])}
                        >
                          <Trash2 className="w-4 h-4" />
                        </Button>
                      )}
                    </div>
                  </CardHeader>

                  <AnimatePresence>
                    {showConsole && (
                      <motion.div
                        initial={{ height: 0, opacity: 0 }}
                        animate={{ height: 'auto', opacity: 1 }}
                        exit={{ height: 0, opacity: 0 }}
                      >
                        <CardContent>
                          <ScrollArea className="h-37.5 bg-slate-950 rounded-lg p-3">
                            {consoleLogs.length === 0 ? (
                              <p className="text-slate-500 text-sm">
                                ...
                              </p>
                            ) : (
                              <div className="space-y-1">
                                {consoleLogs.map((log, i) => (
                                  <div key={i} className="text-sm font-mono">
                                    <span className="text-slate-500 text-xs">
                                      {new Date(
                                        log.timestamp
                                      ).toLocaleTimeString()}
                                    </span>
                                    <span
                                      className={`ml-2 ${
                                        log.type === 'error'
                                          ? 'text-red-400'
                                          : log.type === 'warn'
                                            ? 'text-yellow-400'
                                            : 'text-green-400'
                                      }`}
                                    >
                                      {log.message}
                                    </span>
                                  </div>
                                ))}
                              </div>
                            )}
                          </ScrollArea>
                        </CardContent>
                      </motion.div>
                    )}
                  </AnimatePresence>
                </Card>
              </motion.div>
            )}
          </div>
        </div>
      </div>
      <Footer />
      {/*  */}
      <Dialog open={showSaveDialog} onOpenChange={setShowSaveDialog}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle></DialogTitle>
          </DialogHeader>
          <div className="space-y-4 pt-4">
            <div>
              <Label htmlFor="snippet-name"></Label>
              <Input
                id="snippet-name"
                value={snippetName}
                onChange={e => setSnippetName(e.target.value)}
                placeholder="..."
              />
            </div>
            <div className="flex justify-end gap-2">
              <Button
                variant="outline"
                onClick={() => setShowSaveDialog(false)}
              >
                
              </Button>
              <Button onClick={saveSnippet}>
                <Save className="w-4 h-4 mr-2" />
                
              </Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>
      {/*  */}
      <Dialog open={showSettings} onOpenChange={setShowSettings}>
        <DialogContent className="max-w-md">
          <DialogHeader>
            <DialogTitle></DialogTitle>
          </DialogHeader>
          <div className="space-y-6 pt-4">
            <div className="space-y-2">
              <Label>: {editorSettings.fontSize}px</Label>
              <Slider
                value={[editorSettings.fontSize]}
                onValueChange={v =>
                  setEditorSettings(prev => ({ ...prev, fontSize: v[0] }))
                }
                min={10}
                max={24}
                step={1}
              />
            </div>

            <div className="space-y-2">
              <Label>Tab </Label>
              <Select
                value={String(editorSettings.tabSize)}
                onValueChange={v =>
                  setEditorSettings(prev => ({ ...prev, tabSize: Number(v) }))
                }
              >
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="2">2 </SelectItem>
                  <SelectItem value="4">4 </SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="flex items-center justify-between">
              <Label></Label>
              <Switch
                checked={editorSettings.minimap}
                onCheckedChange={v =>
                  setEditorSettings(prev => ({ ...prev, minimap: v }))
                }
              />
            </div>

            <div className="flex items-center justify-between">
              <Label></Label>
              <Switch
                checked={editorSettings.wordWrap === 'on'}
                onCheckedChange={v =>
                  setEditorSettings(prev => ({
                    ...prev,
                    wordWrap: v ? 'on' : 'off',
                  }))
                }
              />
            </div>

            <div className="flex items-center justify-between">
              <Label></Label>
              <Switch
                checked={editorTheme === 'vs-dark'}
                onCheckedChange={v => setEditorTheme(v ? 'vs-dark' : 'light')}
              />
            </div>

            <div className="flex justify-end">
              <Button onClick={() => setShowSettings(false)}></Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>
    </div>
  )
}
